export function sort(todos) {
    var finish = [];
    var nomal = [];
    var star = [];
    todos.map(function (item) {
        if (item.star) {
            star.push(item);
            return item;
        }
        if (!item.star && !item.finish) {
            nomal.push(item);
            return item;
        }
        finish.push(item);
        return item;
    });
    finish.sort(function (a, b) { return new Date(b.modifiedTime).valueOf() - new Date(a.modifiedTime).valueOf(); });
    nomal.sort(function (a, b) { return new Date(b.modifiedTime).valueOf() - new Date(a.modifiedTime).valueOf(); });
    star.sort(function (a, b) { return new Date(b.modifiedTime).valueOf() - new Date(a.modifiedTime).valueOf(); });
    return star.concat(nomal).concat(finish);
}



